import React from "react";
import PropTypes from "prop-types";
//import AddCategory from './AddCategory';
import {
  Card,
  CardHeader,
  CardBody,
  Form,
  FormGroup,
  FormInput,
  Button,
  Col,
  Row,
  FormSelect,
  FormTextarea,
  InputGroup
} from "shards-react";
import HttpService from "../../../utils/API";
//import LoaderSmall from "../../Loaders/LoaderSmall";

const _http = new HttpService();

class Invite extends React.Component{
    constructor(){
        super();
        this.state ={
            user: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : {},
            title: '',
            type: 0,
            errorMessage: '',
            disable: false,
            requestPending: false,
            subscribers: [],
            searchQuery: "",
            subscriber: '',
            message: '',
            role: '',
            loading: false
        }
    }

    searchInput = (e) => {
        let value = e.target.value;
       this.setState({ searchQuery: value });
      } 

    handleMesage = (e) => {
        this.setState({message: e.target.value})
    }

    handleRole = (e) => {
        this.setState({role: e.target.value})
    }
      
      searchInput = (e) => {
        let value = e.target.value;
       this.setState({ searchQuery: value, subscriber: value });
      } 
      
      getFilteredSubscriberList() {
        return !this.state.searchQuery || !this.state.subscribers
        ? null
        : this.state.subscribers.filter(user => {
                return  user.email.toLowerCase().includes(this.state.searchQuery.toLowerCase());
            }
          );
      }  
      
      selectSubscriber = (subscriber) => {
          this.setState({
              subscriber: subscriber.email,
              searchQuery: null
        })
          document.getElementById('subscriber').value = subscriber.firstname + " " + subscriber.lastname;
    }
  

    handlePublish = (event) => {
        event.preventDefault();
        this.setState({ loading: true})
        //console.log(this.state.subscriber);
        const url = `user/invite`;
        const payload = {
            email: this.state.subscriber,
            role: this.state.role,
            message: this.state.message
        }
        //console.log(payload);
      _http.sendPost(url, payload)
      .then(response => {
       // console.log(response)
        if(response.status === 'success'){
          this.setState({ loading: false})
          _http.notify(response.message, "success");
        }else{
        this.setState({ loading: false})
        _http.notify(response.message);
        }
      })
    }

    componentDidMount() {
        if( this.state.user){
          this.getSubscribers();
        }
      }

    render(){
        const { title } = this.props;
        const { user,loading} = this.state;
        const subscriberList = this.getFilteredSubscriberList();

        return (
            <Row>
            <Col lg="8" className="pb-4">
            <Card small className="h-100">
                {/* Card Header */}
                <CardHeader className="border-bottom">
                <h6 className="m-0">{title}</h6>
                </CardHeader>

                <CardBody className="d-flex flex-column">
                <Form onSubmit={this.handlePublish} autocomplete="off">
                <FormGroup>
                <label htmlFor="title">Subscriber Name</label>
                <InputGroup className="mb-3">
                    <FormInput type="text" id="subscriber" placeholder="search for Subscribers based on filter..." onInput={this.searchInput}/>
                        {/* <InputGroupAddon type="append">
                            <FormSelect onChange={this.searchFilter}>
                            <option vlaue="all">All</option>
                            <option value="name">Name</option>
                            <option value="email">Email</option>
                            </FormSelect> 
                            </InputGroupAddon> */}
                        </InputGroup>
                </FormGroup>
                <ul style={{border : '0.2px solid grey'}}>
                    {subscriberList ?
                    subscriberList.map((subscriber, idx) => {
                        return idx < 5 ? <li key={idx} onClick={() => this.selectSubscriber(subscriber)} className="link pointer dim">{subscriber.email}</li> : null
                    })
                :
                null}
                </ul>
                    <Row>
                        <Col md="6">
                        <FormGroup>
                            <label htmlFor={user.id}>Role</label>
                            <FormSelect id={user.id} onChange={this.handleRole} required>
                            <option value={null}>Select...</option>
                            <option value='99'>Vendor</option>
                            <option value='100'>Coach</option>
                            </FormSelect>
                        </FormGroup>
                        </Col>
                        <Col md="6" className="pb-4">
                       
                        </Col>
                    </Row>     
                    <FormGroup>
                          <label htmlFor="message">Invitation message</label>
                          <FormTextarea rows="7" placeholder="message" onChange={this.handleMesage} required/>
                      </FormGroup>
                      <FormGroup>
                        <Button type="submit" theme="success" disabled={loading}>Send Invitation</Button>
                    </FormGroup>
                </Form>
                </CardBody>
            </Card>
             </Col>
             <Col lg="4">
                    {/* {<AddCategory/>} */}
             </Col>
           </Row> 
            );
        }

        getSubscribers = () => {
            const url = `account/user/list?role=87`
          
            _http.sendGet(url)
            .then(response => {
             //console.log(response)
              if(response.status === 'success'){
                this.setState({ subscribers: response.data, loading: false})
              }else{
              this.setState({ loading: false})
              _http.notify(response.message);
              }
            })
        }
       
    }

Invite.propTypes = {
  /**
   * The component's title.
   */
  title: PropTypes.string
};

Invite.defaultProps = {
  title: "New Invite"
};

export default Invite;