import React from "react";

import HttpService from "../../../../utils/API";
import UserProfile from "../../../../views/UserProfile";
import queryString from 'querystring';
import { withRouter } from "react-router-dom";
import Loader from "../../../Loaders/Loader";

const _http = new HttpService();

class UserDetails extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      roles: [],
      loading: false,
      userEmail: null
    }
  }

  handleRole = (event) => {
    const userId = event.target.id;
    const roleId = event.target.value;
    const payload = {
      user_id: userId,
      role_id: roleId
    }
    
    this.props.updateRole(payload);
  }

  componentDidMount = () => {
    const params = queryString.parse(this.props.location.search)
    this.setState({userEmail: params['?email']})

    const url = 'account/role/list';
    _http.sendGet(url)
    .then(res => {
        this.setState({
          roles: res.data
        })
    })
  }

componentWillUnmount = () => {
  this.abortController.abort();
};

// componentDidUpdate(prevProps, prevState){
//   if(prevProps.user !== this.props.user){
//     this.setState({showViews: views});
//     this.showContent(this.state.path);
//   }
// }

abortController = new window.AbortController(); 

render(){
  const {toggle, open, user, pending} = this.props;
  const { roles } = this.state;
  //console.log(roles)
  
      return (
        <>
            {!this.state.userEmail ? <Loader/> :
            <div> 
                <UserProfile
                    user={this.state.userEmail}
                />
            </div>                                                                                  
            }
        </>
      );
    }
  }

  export default withRouter(UserDetails);