import React, { useEffect, useContext, createContext, useReducer, useState } from 'react';
import HttpService from '../utils/API';
import { sidenavReducer } from '../reducers/sidenavReducer';
import navItem from '../data/sidebar-nav';


export const SidenavContext = createContext();

const _http = new HttpService();
const initialState = {
  data: null
} 

const SidenavContextProvider = (props) => {
    
    //console.log(user)
    const [items, dispatchItem] = useReducer(sidenavReducer, initialState, () => {
        const data = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : null;
        if(data){
            //console.log(data.role.id);
            if(data.role.id === 75){
                return {data: navItem.admin()}
            }
            if(data.role.id === 99 || data.role.id === 103){
                return  {data: navItem.vendor(data.role.id)};
            }
            if(data.role.id === 100){
              return { data: navItem.coach()}
            }
        }
          return initialState;
    });

//   useEffect(()=> {
//     const { user } = useContext(AuthContext)
//     const url = `account/user/list?email=${user.data.email}`;
//     _http.sendGet(url)
//     .then( response => {
//     if(response.status === 'success'){
//         let userProfile = {
//             data :{...response.data[0]}
//         }
//         return userProfile
//     }else{
//         _http.notify(response.message);
        
//         return initialState;
//     }
// })
//   })

  // const logout = () => {
  //   localStorage.clear();
  //   //setIsAuthenticated(false);
  //  };
//console.log(items)
  return ( 
    <SidenavContext.Provider value={{items, dispatchItem}}>
      {props.children}
    </SidenavContext.Provider>
   );
}
 
export default SidenavContextProvider;


