import constants from './constants';
// import User from '../middlewares/user.middleware';
// const user = new User();
import navItems from '../data/sidebar-nav';

export const sidenavReducer = (state, action) => {
    //console.log(action.id)
    switch(action.type){
        case constants.ADMIN:
            // const payload = await user.getUser(action.data.id);
            // console.log(payload)
            return { 
                ...state,
                data: navItems.admin(),
            };
            case constants.COACH:
            // const payload = await user.getUser(action.data.id);
            // console.log(payload)
            return { 
                ...state,
                data: navItems.coach(),
            };
            case constants.VENDOR:
            // const payload = await user.getUser(action.data.id);
            // console.log(payload)
            return { 
                ...state,
                data: navItems.vendor(action.id),
            }
        default:
            return state
    }
}