import React, { Component } from "react";
import { Container, Row, Col, InputGroup, InputGroupAddon,InputGroupText,FormSelect, Button } from "shards-react";
import { withRouter } from "react-router-dom";

import PageTitle from "../components/common/PageTitle";
import UserDetails from "../components/Profile/UserDetails";
import UserAccountDetails from "../components/Profile/UserAccountDetails";
import HttpService from "../utils/API";
import Loader from "../components/Loaders/Loader";
import { ProfileContext } from "../contexts/ProfileContext";
import constants from "../reducers/constants";
import LoaderSmall from "../components/Loaders/LoaderSmall";

const _http = new HttpService();

class UserProfile extends Component{
  constructor(props){
    super(props);
    this.state = {
      user :localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
      userData: {},
      loading: true,
      categories: [],
      countries: [],
      requestPending: '',
      roles: [],
      pending: false
    }
  }

  componentDidMount(){
    this.getUser();
    this.getRoles();
  }

  // componentDidUpdate(){
  //   this.getUser();
  // }

 getUser = () => {
  this.setState({ loading: true})
  const user = this.props.user ? this.props.user : this.state.user.email
  const { dispatch } = this.context;
  const url = `account/user/list?email=${user}`;
    _http.sendGet(url)
    .then(response => {
      if(response.status === 'success'){
       // console.log("------------", response.data[0])
        if(!this.props.user)dispatch({type: constants.UPDATE_PROFILE, data: response.data[0]});
        this.setState({ userData: response.data[0], loading: false})
      }else{
      this.setState({ loading: false})
      _http.notify(response.message);
      }
    })
  }

  getCategories = () => {
    if(!this.state.categories[0]){
      const url = 'content/category/list';
      _http.sendGet(url)
      .then(response => {
          response.data ?
          this.setState({ categories: response.data})
          :
          _http.notify(response.message);
      })
  }
  }

  getRoles = () => {
    const url = 'account/role/list';
    _http.sendGet(url)
    .then(res => {
        this.setState({
          roles: res.data
        })
    })
   }

getCountries = () => {
  if(!this.state.countries[0]){
    const url = 'region/country/list';
    _http.sendGet(url)
    .then(response => {
        response.data ?
        this.setState({ countries: response.data})
        :
        _http.notify(response.message);
    })
}
}

updateProfile = (payload, id) => {
  this.setState({ requestPending: 'profile'})
  const url = `account/user/${id}`;
  _http.sendPut(url, payload)
  .then(response => {
      this.setState({ requestPending: '', disable: false });
      // if(response.data ){
          let type = "";
          if(response.status === "success"){
            this.setState({ requestPending: ''})
              type = "success";
              _http.notify("Profile updated successfully", type);
              this.getUser();
          }else{
            this.setState({ requestPending: ''})
              type = "warn";
              _http.notify(response.message, type)
          }
      
  });
} 

updatePitch = (payload, id) => {
  this.setState({ requestPending: 'pitch'})
  const url = 'media/manager/video/single/create';
   _http.sendPost(url, payload)
   .then(res => {
       //console.log(res)
       if(res.data){
        // console.log(res.data)
           const payload = {
               "pitch_video_link" : res.data.url,
           }
           this.setState({requestPending: '' })
           //const id = this.props.user.id;
           this.updateProfile(payload, id);
       }else{
           _http.notify(res.message)
           this.setState({requestPending: '' })
       }
   });
 }




updatePhoto = (payload, id=null) => {
    this.setState({ requestPending: 'photo'})
  const url = id ? `user/profile/photo/${id}`: `user/profile/photo/`;
  _http.sendPut(url, payload)
  .then(response => {
    let type = "";
    if(response.status === "success"){
      this.setState({ requestPending: ''})
        type = "success";
        _http.notify("Profile updated successfully", type);
        this.getUser();
    }else{
      this.setState({ requestPending: ''})
        type = "warn";
        _http.notify(response.message, type)
    }

  });   
}


updateRole = (event) => {
  this.setState({ pending: true});
  const payload ={
    user_id : event.target.id,
    role_id : event.target.value
  }
  const url = "account/user/role/assign";

  _http.sendPost(url, payload)
  .then(response => {
    //console.log(response)
    this.setState({ pending: false });
    let type = "";
    if(response.status === "success"){
        type = "success";
        _http.notify(response.message, type)
        this.getUser();
    }else{
        type = "warn";
        _http.notify(response.message, type)
    }
});

}


viewWallet = (event) => {
  const id = event.target.id;
  this.props.history.push(`/account/wallet/?id=${id}`);
}

  render(){
    const { userData, pending } = this.state;
    let content = <Loader />
    return (
      <Container fluid className="main-content-container px-4">
        <Row noGutters className="page-header">
          <PageTitle
            title={this.props.user ? userData.firstname + userData.lastname : "My Profile"}
            subtitle="Overview"
            md="12"
            className="ml-sm-auto mr-sm-auto"
          />
        </Row>
        {!this.state.loading ?
        <div>
          <div className="mx-1 d-flex justify-content-end">
            
            <div>
                  
                  <InputGroup className="mb-3">
                  <InputGroupAddon type="prepend">
                      <InputGroupText className="bg-green text-dark">{pending ? <LoaderSmall/> : "Role"}</InputGroupText>
                  </InputGroupAddon>
                  <FormSelect id={userData.id} onChange={this.updateRole} disabled={pending}>
                  <option value={userData.UserRole.Role.id}>{userData.UserRole.Role.name}</option>
                      {this.state.roles ? this.state.roles.map((role)  => {
                      return(
                          <option key={role.id} value={role.id}>{role.name}</option>
                      )
                      })
                  : ''}
                  </FormSelect>
                  </InputGroup>
              </div>
              <div className="mx-3">
                <Button outline theme="info" className="btn btn-md btn-secondary mb-2 mr-1" onClick={this.viewWallet} id={userData.id}>
                      View Wallet
                  </Button>
              </div>
          </div>
          <Row>
            <Col lg="5">
              <UserDetails 
              user={this.state.userData} 
              pending={this.state.requestPending} 
              updatePhoto={this.updatePhoto}
              updateProfile={this.updateProfile}
              updatePitch = {this.updatePitch}/>
            </Col>
            <Col lg="7">
              <UserAccountDetails 
              user={this.state.userData} 
              getCategories={this.getCategories} 
              categories={this.state.categories} 
              getCountries={this.getCountries} 
              countries={this.state.countries}
              updateProfile={this.updateProfile}
              pending={this.state.requestPending}/>
            </Col>
          </Row>
          </div>
        :<Loader/>}
      </Container>
    );
  }
};
UserProfile.contextType = ProfileContext;

export default withRouter(UserProfile);
