import React from 'react';
import Loader from '../../Loaders/Loader';
import { 
    Container, 
    Row, 
    Col, 
    Card, 
    CardHeader, 
    CardBody, 
    FormInput,
    FormSelect,
  InputGroup,
  InputGroupAddon,
  Button
} from "shards-react"

class WalletDetails extends React.Component{
    constructor(props){
        super(props);
        this.state={
            open: false,
            walletDetails: [],
            searchQuery: null,
            filter: "all"
        }
    }

    currentUser = (index) => {
        return this.props.users[index]
    }

   toggleModal = (event) => {
      
    if(event){
        let userId = parseInt(event.target.id);
        const currentUser = this.props.users.filter(user => user.id === userId)
        this.setState({
           open: !this.state.open,
           user: currentUser[0]
        });
    }
       return this.state.open
       
    }

    searchFilter = (e) => {
        let filter = e.target.value;
        switch(filter){
            case "reference":
                return this.setState({filter: filter});
            case "date":
                return this.setState({filter: filter});
            default:
             return this.setState({filter: "all"})
        }
    }

    searchInput = (e) => {
        let value = e.target.value;
       this.setState({ searchQuery: value });
      } 

    getFilteredWalletList() {
        return !this.state.searchQuery
          ? this.props.walletDetails
          : this.props.walletDetails.filter(details => {
              switch(this.state.filter){
                case "reference":
                    return  details.reference.toLowerCase().includes(this.state.searchQuery.toLowerCase());
                case "date":
                    return  details.createdAt.toLowerCase().includes(this.state.searchQuery.toLowerCase());
                default:
                 return details.reference.toLowerCase().includes(this.state.searchQuery.toLowerCase()) || 
                        details.createdAt.toLowerCase().includes(this.state.searchQuery.toLowerCase())
              }
             
          }
            );
      }     

render(){
    const {loading, typeName } = this.props;
    let walletDetails = this.getFilteredWalletList();
    let i = 1;
    // console.log(users);
    return(

        <Container  className="px-0 py-0" fluid>
            <Row>
            <Col>
                <Card small className="mb-4">
                <CardHeader className="border-bottom">
                    <h6 className="m-0">Wallet Transactions</h6>
                    <InputGroup className="mb-3">
                        <InputGroupAddon type="prepend">
                        <FormSelect onChange={this.searchFilter}>
                        <option vlaue="all">All</option>
                        <option value="reference">Reference</option>
                        {/* <option value="username">Username</option> */}
                        <option value="date">Date</option>
                        </FormSelect> 
                        </InputGroupAddon>
                        <FormInput type="text" placeholder={`search for reference, date...`} onInput={this.searchInput}/>
                    </InputGroup>
                </CardHeader>
                <CardBody className="p-0 pb-3">
                {loading ?
                <Loader />
                :
                walletDetails.length > 0 ?
                    <table className="table mb-0">
                    <thead className="bg-light">
                        <tr>
                        <th scope="col" className="border-0">
                            #
                        </th>
                        <th scope="col" className="border-0">
                            Amount
                        </th>
                        <th scope="col" className="border-0">
                            Status
                        </th>
                        <th scope="col" className="border-0">
                           Reference
                        </th>
                        <th scope="col" className="border-0">
                           Date
                        </th>
                        <th scope="col" className="border-0">
                            Type
                        </th>
                        </tr>
                    </thead>
                    <tbody>
                    {walletDetails.map((details, index)  => {
                            //let userId = `#${user.id}`;
                            //console.log(users);
                            return(
                                <tr key={details.id}>
                                    <td>{i++}</td>
                                    <td>{details.amount}</td>
                                    <td>{details.status ? "complete" : "intitiated"}</td>
                                    <td>{details.reference}</td>
                                    <td>{details.createdAt}</td>
                                    <td>{details.type === "deposit" ? 
                                        <Button outline size="xs" theme="success" id="purchases" className="mb-2 mr-1 p-2" disabled={true}>
                                            {details.type}
                                        </Button>
                                        :
                                        <Button outline size="xs" theme="danger" id="purchases" className="mb-2 mr-1 p-2" disabled={true}>
                                            {details.type}
                                        </Button>
                                        }
                                    </td>
                                </tr> 
                                
                            )
                        })
                    }
                    </tbody>
                    </table>
                :
                <p className="text-center brown" style={{color: 'brown'}}>No transactions have been made by this user</p>
                }
                </CardBody>
                </Card>
            </Col>
            </Row>
        </Container>
    );
}
}

export default WalletDetails;
