export const profileReducer = (state, action) => {

    switch(action.type){
        case constants.UPDATE_PROFILE:
            // const payload = await user.getUser(action.data.id);
            // console.log(payload)
            return { 
                ...state,
                data: action.data,
            }
        default:
            return state
    }
}