import React from "react";
import LoaderSmall from '../Loaders/LoaderSmall';
import {
  Card,
  CardHeader,
  Button,
  ListGroup,
  ListGroupItem,
  FormInput,
  Row,
  Col,
  InputGroup,
  InputGroupAddon
} from "shards-react";
import GetImage from "../common/getImage";
import GetVideo from "../common/GetVideo";
import HttpService from "../../utils/API";
import { ProfileContext } from "../../contexts/ProfileContext";

const _http = new HttpService();

class UserDetails extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      loading: false,
      requestPending: false,
      disabled: false,
      avatar: ''
    }
  }

  componentDidMount(){
    this.setState({avatar:this.context.profile.photo})
  }


handleUpload = (id) => {
 const image = document.getElementById('avatar');
 const formdata = new FormData();
 if(image.files[0]){
  formdata.append("image", image.files[0], ".jpg");
  this.props.updatePhoto(formdata, id)
 }
}

handlePitch = () => {
  const file = document.getElementById('pitch');
  const id = this.props.user.id;
  const formdata = new FormData();
  if(file.files[0]){
  this.setState({requestPending: true});
   formdata.append("video", file.files[0]);
   this.props.updatePitch(formdata, id)
  }
 }


  render() { 
    const { user, pending } = this.props;
    const { profile } = this.context;
    const userData = user ? user : profile.data;
    const userDetails = {
        name:  userData.firstname ? userData.firstname + " " + userData.lastname : " ",
        avatar: require("./../../images/avatars/0.png"),
        jobTitle: "Life Coach",
        performanceReportTitle: "Experience",
        performanceReportValue: 74,
        metaTitle: "Work History",
        metaValue:
          "Lorem ipsum dolor sit amet consectetur adipisicing elit. Odio eaque, quidem, commodi soluta qui quae minima obcaecati quod dolorum sint alias, possimus illum assumenda eligendi cumque?"
      }

      //console.log(userData)
    return (  
      <Card small className="mb-4 pt-3">
        <CardHeader className="border-bottom text-center">
          <div className="mb-3 mx-auto">
          {userData.photo ? 
          <GetImage image={userData.photo}   alt={userDetails.name} height="200px" classname="rounded-circle"/>
          :
          <img
          className="rounded-circle"
          src={userDetails.avatar}
          alt={userDetails.name}
          width="110"
        />
        }
          </div>
          <InputGroup className="mb-3 input-group-sm">
              <FormInput type="file" id="avatar"/>
              <InputGroupAddon type="append">
                <Button size="sm" theme="secondary" className="py-0" onClick={() => this.handleUpload(userData.id)} disabled={pending === 'photo'}>{pending === 'photo' ? <LoaderSmall/> :<i className="material-icons mr-1">save</i>}</Button>
              </InputGroupAddon>
          </InputGroup>
          <h4 className="mb-0">{userDetails.name}</h4>
        
        </CardHeader>
        <ListGroup flush>
         
          <ListGroupItem className="p-4">
            <strong className="text-muted d-block mb-2">
              Pitch video
            </strong>
            <div>
              { userData.pitch_video_link ? <GetVideo width="100%" video={userData.pitch_video_link}/> : ''}
           <br/>
           </div>
           <InputGroup className="mb-3 input-group-sm">
              <FormInput type="file" id="pitch"/>
              <InputGroupAddon type="append">
                <Button size="sm" theme="secondary" className="py-0" onClick={this.handlePitch} disabled={pending === 'pitch'}>{pending === 'pitch' ? <LoaderSmall/> :<i className="material-icons mr-1">save</i>}</Button>
              </InputGroupAddon>
          </InputGroup>
          </ListGroupItem>
        </ListGroup>

        <ListGroup flush>
          <ListGroupItem className="p-4">
          <Row>
            <Col lg="4" md="4" sm="4" xs="4" className="f7 text-primary fw4">
              <i className="material-icons mr-1">person_add</i>0<br/>
              <span>Followers</span>
            </Col>
            <Col md="5" sm="4" xs="4" className="f7 text-primary fw4">
              <i className="material-icons mr-1">how_to_reg</i>{userData.subscriptionCount ? userData.subscriptionCount : 0}<br/>
              <span>Subscriptions</span>
            </Col>
            <Col md="3" sm="4" xs="4" className="f7 text-primary fw4">
              <i className="material-icons mr-1">thumb_up</i>{userData.likeCount ? userData.likeCount.count : 0}<br/>
              <span>Likes</span>
            </Col>
          </Row>
          </ListGroupItem>
        </ListGroup>
      </Card>
    );
  }

}
UserDetails.contextType = ProfileContext;
export default UserDetails;
