import React, { Component } from 'react';
import queryString from 'querystring';

import LoaderSmall from '../../Loaders/LoaderSmall';
import {
    Card,
    CardHeader,
    ListGroup,
    FormInput,
    Button,
    FormGroup,
    FormTextarea,
    Form,
    Col,
    Row,
    Container
  } from "shards-react";
import HttpService from '../../../utils/API';
import CreateCategory from "./CreateCategory";
import GetImage from "../../common/getImage";
import Loader from '../../Loaders/Loader';
import PageTitle from '../../common/PageTitle';
import { withRouter } from 'react-router-dom';
import { confirmAlert } from 'react-confirm-alert';

const _http = new HttpService()

class EditCategory extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            name: '',
            description: '',
            errMessage: '',
            requestPending: false,
            category: {},
            isLoading: true,
            deletePending: false,
            updatePending: false
         };
      }

      handleCategory = (event) => {
        const val = event.target.value;
        const data = (val.trim()).replace(' ', '_');
        const newDetails = {
            ...this.state.category,
          }
          newDetails.name = data;
          this.setState({category: newDetails});
      }

    handleDescription = (event) => {
        const newDetails = {
            ...this.state.category,
          }
          newDetails.description = event.target.value;
          this.setState({category: newDetails});
    }

    componentDidMount(){
       this.getCategory();
    }
    componentWillUnmount = () => this.abortController.abort();

    abortController = new window.AbortController(); 

    getCategory = () => {
        const params = queryString.parse(this.props.location.search)
        const id = params['?id'];
        //console.log(id)
        const url = `content/category/${id}`;
        _http.sendGet(url)
        .then(response => {
            response.data ?
            this.setState({ errorMessage: '', category: response.data, isLoading: false })
            :
            this.setState({ errorMessage: response.message, isLoading: false })
        })
    }

    render() { 
        const {category, isLoading} = this.state;
        return ( 
          <>
          <Container fluid className="main-content-container px-4 pb-4">
            <Row noGutters className="page-header">
              <PageTitle sm="4" title="Edit Category" subtitle="" className="text-sm-left" />
            </Row>
            {isLoading ? 
                    <Loader/>
                :
             <Row className="my-2">
                  
                 <Col lg="3" md="3" sm="3" xs="12"></Col>
                <Col lg="6" md="6" sm="6" xs="12" className="bg-light">
                <ListGroup flush className="p-4">
                <Row className="center my-2">
                    <GetImage image={category.image_url} width="250px"/>
                </Row>
                    <Form onSubmit={this.onSubmitRequest}>
                    <FormGroup>
                            <label htmlFor="image">Display Image</label>
                            <FormInput
                            id="image"
                            type="file"
                            placeholder="image" required={!category.image_url}/>
                        </FormGroup>
                        <FormGroup>
                            <label htmlFor="name">Category Name</label>
                            <FormInput
                                id="name"
                                type="text"
                                defaultValue={category ? category.name : ""}
                                onChange={this.handleCategory} placeholder="Name"required />
                        </FormGroup>
                        <FormGroup>
                        <label htmlFor="description">Description</label>
                            <FormTextarea
                            id="description"
                            rows="5"
                            defaultValue={category
                                    ? category.description : ""}
                            onChange={this.handleDescription} placeholder="Description" required/>
                        </FormGroup>
                        
                        <FormGroup>
                            <Button type="submit" size="md" theme="success" className="mr-2" disabled={this.state.requestPending}>{this.state.updatePending ? <LoaderSmall/> : 'Update'}</Button>
                             <Button onClick={this.handleDelete} size="md" theme="secondary" id={category ? category.id: ""} disabled={this.state.requestPending}>{this.state.deletePending ? <LoaderSmall/> : 'delete'}</Button>
                        </FormGroup>
                </Form>
                </ListGroup>
                </Col>
                <Col lg="3" md="3" sm="3" xs="12"></Col>
            </Row>
    }
    </Container>
       </>
           
         );
    }

    onSubmitRequest = (event) => {
        event.preventDefault();
        this.setState({requestPending: true, updatePending: true});
        const image = document.getElementById('image');

        if(image.files[0]){
            const imageUrl = 'media/manager/image/single/create';
            const imageData =  new FormData();
            const name = this.state.category.name;
            const description =  this.state.category.description;
            //console.log(description);
            imageData.append('title', name)
            imageData.append('description', description)
            imageData.append('image', image.files[0])

                _http.sendPost(imageUrl, imageData)
                .then(response => {
                    let  imageRes = response.data;
                // console.log(imageRes)
                    if(imageRes.status === 1){
                        const payload = {
                            "name": name,
                            "description": description,
                            "image_url": imageRes.url
                        }
                    
                        const categoryId = this.state.category.id;
                        const url = `content/category/${categoryId}`
                        const request = _http.sendPut(url, payload);

                    request.then(response => {
                        this.setState({ requestPending: false, updatePending: false });
                        if(response.data ){
                            //this.setState({requestPending: true});
                            if(response.status === "success"){
                                _http.notify(response.message, "success");
                                document.getElementById('image').value = '';
                                this.getCategory();
                            }else{
                                _http.notify(response.message)
                            }
                        
                        }else{
                            _http.notify(response.message)
                        }
                    });
                    }
                })
                .catch(err => console.log(err))
            }else{
                const categoryId = this.state.category.id;
                const payload = {
                    "name": this.state.category.name,
                    "description": this.state.category.description,
                }
                const url = `content/category/${categoryId}`
                const request = _http.sendPut(url, payload);

                request.then(response => {
                    this.setState({ requestPending: false, updatePending: false });
                    if(response.data ){
                        if(response.status === "success"){
                            _http.notify(response.message, "success")
                            this.getCategory();
                        }else{
                            _http.notify(response.message)
                        }
                    
                    }else{
                        _http.notify(response.message)
                        this.setState({requestPending: false, updatePending: false })
                    }
                });
            }
        }

        handleDelete = (event) => {
            const categoryId = event.target.id;
            confirmAlert({
               title: 'Confirm Delete',
               message: 'Are you sure you want to delete this category?',
               buttons: [
                 {
                   label: 'Yes',
                   onClick: () => this.deleteCategory(categoryId)
                 },
                 {
                   label: 'No',
                   
                 }
               ]
             });
         }
        
         deleteCategory = (id) => {
             const url = `content/category/${id}`;
             this.setState({requestPending: true, deletePending: true});
           _http.sendDelete(url)
           .then(response => {
                this.setState({requestPending: false, deletePending: false});
                let type = "";
                if(response.status === "success"){
                    type = "success";
                    _http.notify(response.message, type);
                    //this.getRoles();
                    this.props.history.push(`/category`)
                }else{
                    type = "warn";
                    _http.notify(response.message, type)
                }
           })
         }
}
 
export default withRouter(EditCategory);
